import * as THREE from 'three';
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js';
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js';
import { BokehPass } from 'three/examples/jsm/postprocessing/BokehPass.js';

class ThreeAnimation {
    constructor() {
        // Configuration
        this.config = {
            grid: {
                spacing: 25,
                pointSize: 1.5,
                minPointSize: 0,
                maxPointSize: 2,
                depthAmplitude: 10
            },
            bokeh: {
                focus: 329,
                aperture: 0.0000,
                maxblur: 0.15
            },
            mouse: {
                interactionDistance: 150,
                displacementMax: 40
            },
            colors: {
                touchdesigner: {
                        close: 0xF3599C,
                        medium: 0x3726CF
                    },
                    design: {
                        close: 0xFF6F29,
                        medium: 0x931B2B
                    },
                    development: {
                        close: 0x987AFF,
                        medium: 0x203CAC
                    }
                },
                videoPlane: {
                    width: 300,
                    height: 400,
                    position: {
                        x: window.innerWidth * 0.1,
                        y: 0,
                        z: 35
                    }
                }
            };

            // Three.js setup
            this.scene = new THREE.Scene();
            this.camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
            this.renderer = new THREE.WebGLRenderer({ alpha: true });
            this.gridPoints = [];
            this.currentCategory = 'touchdesigner';
            this.mouse = new THREE.Vector2();
            this.mouseInScene = new THREE.Vector3();
            this.waterMovementOffset = 50;
            this.isTransitioning = false;
            this.fadeOutAnimation = null;

            // Ajouter les propriétés pour la transition de couleur
            this.targetColors = {
                medium: new THREE.Color(this.config.colors[this.currentCategory].medium),
                close: new THREE.Color(this.config.colors[this.currentCategory].close)
            };
            this.currentColors = {
                medium: new THREE.Color(this.config.colors[this.currentCategory].medium),
                close: new THREE.Color(this.config.colors[this.currentCategory].close)
            };
            this.colorTransitionSpeed = 0.05; // Vitesse de transition (0-1)

            // Post-processing
            this.composer = null;
            this.bokehPass = null;

            // Propriétés pour le plan vidéo
            this.videoTexture = null;
            this.videoMaterial = null;
            this.videoPlane = null;
            this.currentVideo = null;

            this.init();
            // Démarrer avec les points invisibles et les faire apparaître
            this.gridPoints.forEach(point => {
                point.scale.set(0, 0, 0);
            });
            this.fadeInPoints(600);
        }

        init() {
            // Setup renderer
            this.renderer.setSize(window.innerWidth, window.innerHeight);
            this.renderer.setClearColor(0x000000, 0);
            this.renderer.outputColorSpace = THREE.LinearSRGBColorSpace;
            document.querySelector('main').appendChild(this.renderer.domElement);
            this.camera.position.z = 385;

            // Create grid
            this.createGrid();

            // Setup post-processing
            this.setupPostProcessing();

            // Event listeners
            window.addEventListener('resize', () => this.onResize());
            window.addEventListener('mousemove', (e) => this.onMouseMove(e));

            // Start animation
            this.animate();

            // Créer le plan vidéo
            this.createVideoPlane();
        }

        setupPostProcessing() {
            this.composer = new EffectComposer(this.renderer);

            // Add render pass
            const renderPass = new RenderPass(this.scene, this.camera);
            this.composer.addPass(renderPass);

            // Add bokeh pass with enhanced parameters
            this.bokehPass = new BokehPass(this.scene, this.camera, {
                focus: this.config.bokeh.focus,
                aperture: this.config.bokeh.aperture,
                maxblur: this.config.bokeh.maxblur,
                width: window.innerWidth,
                height: window.innerHeight,
                resolution: new THREE.Vector2(1024, 1024),
                rings: 3,
                samples: 2,
                showFocus: false
            });
            this.composer.addPass(this.bokehPass);
        }

        createGrid() {
            const gridWidth = Math.floor(window.innerWidth / this.config.grid.spacing);
            const gridHeight = Math.floor(window.innerHeight / this.config.grid.spacing);

            for (let i = 0; i < gridWidth; i++) {
                for (let j = 0; j < gridHeight; j++) {
                    const x = (i - gridWidth / 2) * this.config.grid.spacing;
                    const y = (j - gridHeight / 2) * this.config.grid.spacing;

                    const geometry = new THREE.SphereGeometry(this.config.grid.pointSize, 8, 8);
                    const material = new THREE.MeshBasicMaterial({
                        color: this.config.colors[this.currentCategory].medium,
                        transparent: true,
                        opacity: 1,
                        blending: THREE.NormalBlending
                    });

                    const point = new THREE.Mesh(geometry, material);
                    point.position.set(x, y, 0);
                    point.originalPosition = new THREE.Vector3(x, y, 0);

                    this.gridPoints.push(point);
                    this.scene.add(point);
                }
            }
        }

        updateColors(category) {
            this.currentCategory = category;
            const colors = this.config.colors[category];

            // Mettre à jour les couleurs cibles
            this.targetColors.medium.setHex(colors.medium);
            this.targetColors.close.setHex(colors.close);

            // Mettre à jour les couleurs du shader seulement si le matériel est initialisé
            if (this.videoMaterial && this.videoMaterial.uniforms && 
                this.videoMaterial.uniforms.colorClose && 
                this.videoMaterial.uniforms.colorMedium) {
                this.videoMaterial.uniforms.colorClose.value.setHex(colors.close);
                this.videoMaterial.uniforms.colorMedium.value.setHex(colors.medium);
            }
        }

        updatePoints() {
            this.waterMovementOffset += 0.007;

            // Interpoler les couleurs actuelles vers les couleurs cibles
            this.currentColors.medium.lerp(this.targetColors.medium, this.colorTransitionSpeed);
            this.currentColors.close.lerp(this.targetColors.close, this.colorTransitionSpeed);

            let averageZ = 0;

            // Mettre à jour les points de la grille principale
            this.gridPoints.forEach(point => {
                // Water movement avec composante Z
                const waterX = Math.sin(point.originalPosition.x * 0.02 + this.waterMovementOffset) * 5;
                const waterY = Math.cos(point.originalPosition.y * 0.02 + this.waterMovementOffset) * 5;
                const waterZ = Math.sin(point.originalPosition.x * 0.01 + point.originalPosition.y * 0.01 + this.waterMovementOffset)
                    * this.config.grid.depthAmplitude;
                const waterMovement = new THREE.Vector3(waterX, waterY, waterZ);

                // Mouse interaction
                const distanceToMouse = point.position.distanceTo(this.mouseInScene);
                let mouseDisplacement = new THREE.Vector3();

                if (distanceToMouse < this.config.mouse.interactionDistance) {
                    const direction = point.position.clone().sub(this.mouseInScene).normalize();
                    const strength = 1 - (distanceToMouse / this.config.mouse.interactionDistance);
                    mouseDisplacement = direction.multiplyScalar(strength * this.config.mouse.displacementMax);

                    // Transition de couleur avec les couleurs interpolées
                    const color = new THREE.Color().copy(this.currentColors.medium);
                    color.lerp(this.currentColors.close, strength);
                    point.material.color.copy(color);
                } else {
                    point.material.color.copy(this.currentColors.medium);
                }

                // Facteur de transition pour le fadeOut
                const transitionFactor = this.isTransitioning ? point.scale.x : 1;

                // Update position avec le facteur de transition
                const targetPosition = point.originalPosition.clone()
                    .add(waterMovement.multiplyScalar(transitionFactor))
                    .add(mouseDisplacement.multiplyScalar(transitionFactor));
                point.position.lerp(targetPosition, 0.5);

                // Calculer la taille du point en fonction de sa position Z
                const normalizedZ = (point.position.z + this.config.grid.depthAmplitude) / (2 * this.config.grid.depthAmplitude);
                const size = this.config.grid.minPointSize + 
                    (this.config.grid.maxPointSize - this.config.grid.minPointSize) * normalizedZ;

                // Ne pas mettre à jour la scale ici car elle est gérée par fadeOutPoints
                if (!this.isTransitioning) {
                    point.scale.setScalar(size);
                }
                point.material.opacity = 0.85 + normalizedZ * 0.15;

                averageZ += point.position.z;
            });

            // Mettre à jour le focus du bokeh
            if (this.bokehPass) {
                averageZ /= this.gridPoints.length;
                const targetFocus = this.camera.position.z + averageZ;
                this.bokehPass.uniforms['focus'].value += (targetFocus - this.bokehPass.uniforms['focus'].value) * 0.05;
            }
        }

        onMouseMove(event) {
            this.mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
            this.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;

            // Mettre à jour la position de la souris pour le shader
            if (this.videoMaterial && this.videoMaterial.uniforms) {
                this.videoMaterial.uniforms.mousePosition.value.set(
                    event.clientX / window.innerWidth,
                    1.0 - (event.clientY / window.innerHeight)
                );
                
                // Augmenter progressivement l'influence de la souris
                const targetInfluence = 1.0;
                const currentInfluence = this.videoMaterial.uniforms.mouseInfluence.value;
                this.videoMaterial.uniforms.mouseInfluence.value += (targetInfluence - currentInfluence) * 0.1;
            }

            const vector = new THREE.Vector3(this.mouse.x, this.mouse.y, 0.5);
            vector.unproject(this.camera);
            vector.sub(this.camera.position).normalize();
            const distance = -this.camera.position.z / vector.z;
            this.mouseInScene.copy(this.camera.position).add(vector.multiplyScalar(distance));
        }

        onResize() {
            const width = window.innerWidth;
            const height = window.innerHeight;

            this.camera.aspect = width / height;
            this.camera.updateProjectionMatrix();
            this.renderer.setSize(width, height);

            // Update composer size
            if (this.composer) {
                this.composer.setSize(width, height);
            }

            // Recreate grid
            this.gridPoints.forEach(point => this.scene.remove(point));
            this.gridPoints = [];
            this.createGrid();
        }

        animate() {
            // Animation normale des points (toujours active)
            this.waterMovementOffset += 0.01;
            this.updatePoints();

            // Mise à jour du temps pour l'animation du plan vidéo
            if (this.videoMaterial && this.videoMaterial.uniforms) {
                this.videoMaterial.uniforms.time.value += 0.01;
            }

            // Rendu de la scène
            this.composer.render();
            requestAnimationFrame(this.animate.bind(this));
        }

        createVideoPlane() {
            // Créer une texture vidéo vide initialement
            const video = document.createElement('video');
            video.autoplay = true;
            video.loop = true;
            video.muted = true;
            video.playsInline = true;
            video.crossOrigin = 'anonymous';
            
            this.videoTexture = new THREE.VideoTexture(video);
            this.videoTexture.minFilter = THREE.LinearFilter;
            this.videoTexture.magFilter = THREE.LinearFilter;
            this.videoTexture.format = THREE.RGBAFormat;
            this.videoTexture.colorSpace = THREE.SRGBColorSpace;
            this.videoTexture.generateMipmaps = false;

            // Configuration de la grille de contrôle
            const segmentsX = 20;
            const segmentsY = 25;
            const width = this.config.videoPlane.width;
            const height = this.config.videoPlane.height;

            // Créer une géométrie plus détaillée pour la déformation fluide
            const planeGeometry = new THREE.PlaneGeometry(
                width,
                height,
                segmentsX,
                segmentsY
            );

            // Créer un shader personnalisé pour l'effet de tuiles
            const customMaterial = new THREE.ShaderMaterial({
                uniforms: {
                    videoTexture: { value: this.videoTexture },
                    time: { value: 0.0 },
                    tileSize: { value: new THREE.Vector2(20.0, 20.0) }, // Taille des tuiles
                    mousePosition: { value: new THREE.Vector2(0.5, 0.5) },
                    mouseInfluence: { value: 500.0 },
                    displacementIntensity: { value: 75.0 } // Intensité du déplacement
                },
                vertexShader: `
                    varying vec2 vUv;
                    varying float vDisplacement;
                    
                    uniform float time;
                    uniform vec2 tileSize;
                    uniform vec2 mousePosition;
                    uniform float mouseInfluence;
                    uniform float displacementIntensity;
                    
                    void main() {
                        vUv = uv;
                        
                        // Calculer la position de la tuile
                        vec2 tilePosition = floor(vUv * tileSize) / tileSize;
                        
                        // Distance à la souris
                        float mouseDistance = length(vUv - mousePosition);
                        float mouseEffect = (1.0 - smoothstep(0.05, 0.25, mouseDistance)) * mouseInfluence;
                        
                        // Effet de vague basé sur le temps et la position (amplitude réduite)
                        float waveX = sin(position.x * 0.02 + time * 2.0) * 3.0;
                        float waveY = cos(position.y * 0.02 + time * 2.0) * 3.0;
                        float waveZ = sin(position.x * 0.01 + position.y * 0.01 + time * 2.0) * 3.0;
                        
                        // Déplacement basé sur la position de la tuile et les vagues
                        float displacement = sin(tilePosition.x * 10.0 + time) * 
                                          cos(tilePosition.y * 10.0 + time) * 
                                          displacementIntensity * mouseEffect;
                        
                        // Appliquer le déplacement et les vagues
                        vec3 newPosition = position;
                        newPosition.x += waveX;
                        newPosition.y += waveY;
                        newPosition.z += displacement + waveZ;
                        
                        vDisplacement = displacement;
                        
                        gl_Position = projectionMatrix * modelViewMatrix * vec4(newPosition, 1.0);
                    }
                `,
                fragmentShader: `
                    uniform sampler2D videoTexture;
                    varying vec2 vUv;
                    varying float vDisplacement;
                    
                    void main() {
                        // Force de l'aberration basée sur le déplacement
                        float aberrationStrength = abs(vDisplacement) * 0.003;
                        
                        // Décalage des canaux RGB
                        vec2 redOffset = vUv + vec2(aberrationStrength, 0.0);
                        vec2 greenOffset = vUv;
                        vec2 blueOffset = vUv - vec2(aberrationStrength, 0.0);
                        
                        // Échantillonner la texture pour chaque canal
                        float r = texture2D(videoTexture, redOffset).r;
                        float g = texture2D(videoTexture, greenOffset).g;
                        float b = texture2D(videoTexture, blueOffset).b;
                        float a = texture2D(videoTexture, vUv).a;
                        
                        // Combiner les canaux avec un effet de luminosité
                        float brightness = 1.0 + abs(vDisplacement) * 0.05;
                        vec3 color = vec3(r, g, b) * brightness;
                        
                        gl_FragColor = vec4(color, a);
                    }
                `,
                transparent: true,
                blending: THREE.AdditiveBlending,
                side: THREE.DoubleSide
            });

            this.videoMaterial = customMaterial;

            // Créer le plan vidéo principal
            this.videoPlane = new THREE.Mesh(planeGeometry, this.videoMaterial);
            this.videoPlane.position.set(
                this.config.videoPlane.position.x,
                this.config.videoPlane.position.y,
                this.config.videoPlane.position.z
            );
            this.scene.add(this.videoPlane);

            // Créer les points de contrôle invisibles
            this.controlPoints = [];
            const controlGeometry = new THREE.SphereGeometry(2);
            const controlMaterial = new THREE.MeshBasicMaterial({
                color: 0xffffff,
                transparent: true,
                opacity: 0
            });

            // Stocker les positions originales des vertices pour la déformation
            this.originalVertices = planeGeometry.attributes.position.array.slice();

            // Créer une grille de points de contrôle plus espacée
            const controlSpacingX = width / (segmentsX / 2);
            const controlSpacingY = height / (segmentsY / 2);

            for (let i = 0; i <= segmentsX / 2; i++) {
                for (let j = 0; j <= segmentsY / 2; j++) {
                    const x = (i * controlSpacingX) - (width / 2) + this.config.videoPlane.position.x;
                    const y = (j * controlSpacingY) - (height / 2) + this.config.videoPlane.position.y;
                    const z = this.config.videoPlane.position.z;

                    const controlPoint = new THREE.Mesh(controlGeometry, controlMaterial);
                    controlPoint.position.set(x, y, z);
                    controlPoint.originalPosition = new THREE.Vector3(x, y, z);
                    
                    this.controlPoints.push(controlPoint);
                    this.scene.add(controlPoint);
                }
            }

            this.currentVideo = video;
        }

        updateVideoTexture(videoSrc) {
            if (!this.videoPlane || !this.videoMaterial) return;
            if (this.isTransitioning) return;
            this.isTransitioning = true;

            if (this.currentVideo) {
                this.currentVideo.pause();
            }

            // Changer directement la source vidéo
            this.currentVideo.src = videoSrc;
            this.currentVideo.onloadeddata = () => {
                this.currentVideo.play().then(() => {
                    this.isTransitioning = false;
                }).catch(error => {
                    console.error('Erreur de lecture vidéo:', error);
                    this.isTransitioning = false;
                });
            };

            this.currentVideo.onerror = () => {
                console.error('Erreur de chargement vidéo');
                this.isTransitioning = false;
            };
        }

        fadeOutPoints(duration = 600) {
            console.log('fadeOutPoints anim');
            if (this.isTransitioning) return;
            this.isTransitioning = true;
        
            const startTime = performance.now();
            const initialScales = this.gridPoints.map(point => point.scale.x);
        
            const animate = (currentTime) => {
                const elapsed = currentTime - startTime;
                const progress = Math.min(elapsed / duration, 1);
        
                // New easing function for a dramatically different curve
                const easeInExpo = progress === 0 ? 0 : Math.pow(2, 10 * (progress - 1));
        
                this.gridPoints.forEach((point, index) => {
                    const scale = initialScales[index] * (1 - easeInExpo);
                    point.scale.set(scale, scale, scale);
                });
        
                if (progress < 1) {
                    this.fadeOutAnimation = requestAnimationFrame(animate);
                }
            };
        
            this.fadeOutAnimation = requestAnimationFrame(animate);
        }

        fadeInPoints(duration = 600) {
            console.log('fadeInPoints anim');
            if (this.isTransitioning) return;
            this.isTransitioning = true;

            const startTime = performance.now();
            const targetScales = this.gridPoints.map(point => {
                // Calculer la taille cible basée sur la position Z
                const normalizedZ = (point.position.z + this.config.grid.depthAmplitude) / (2 * this.config.grid.depthAmplitude);
                return this.config.grid.minPointSize + 
                    (this.config.grid.maxPointSize - this.config.grid.minPointSize) * normalizedZ;
            });

            const animate = (currentTime) => {
                const elapsed = currentTime - startTime;
                const progress = Math.min(elapsed / duration, 1);
                
                // Même fonction d'easing que fadeOut mais inversée
                const easeInCubic = Math.pow(progress, 3);
                
                this.gridPoints.forEach((point, index) => {
                    const scale = targetScales[index] * easeInCubic;
                    point.scale.set(scale, scale, scale);
                });

                if (progress < 1) {
                    this.fadeInAnimation = requestAnimationFrame(animate);
                } else {
                    this.isTransitioning = false;
                }
            };

            this.fadeInAnimation = requestAnimationFrame(animate);
        }
    }

    // Export instance
    const threeAnimation = new ThreeAnimation();
export default threeAnimation; 