import threeAnimation from './three-animation.js';

// L'animation démarre automatiquement car elle est initialisée dans three-animation.js
window.addEventListener('beforeunload', () => {
    // Cleanup si nécessaire
    if (threeAnimation) {
        // Supprimer les event listeners
        window.removeEventListener('resize', threeAnimation.onResize);
        window.removeEventListener('mousemove', threeAnimation.onMouseMove);
        
        // Nettoyer la scène
        threeAnimation.gridPoints.forEach(point => {
            point.geometry.dispose();
            point.material.dispose();
        });
        
        // Supprimer le canvas
        const canvas = document.querySelector('canvas');
        if (canvas) {
            canvas.remove();
        }
    }
});

const transitionTime = 700;
const fadeOutDuration = 700; // Durée de l'animation de disparition

// Configuration des éléments par page
const pageConfig = {
    'index.html': {
        top: 'header',
        center: '.container',
        bottom: 'footer'
    },
    'projects.html': {
        top: 'header',
        center: '.menu-projects-content',
        bottom: 'footer'
    },
    'about.html': {
        top: 'header',
        center: '.about-container',
        bottom: 'footer'
    },
    'contact.html': {
        top: 'header',
        center: '.contact-container',
        bottom: 'footer'
    }
};

// Fonction pour obtenir la configuration de la page actuelle
function getCurrentPageConfig() {
    const path = window.location.pathname;
    const page = path.substring(path.lastIndexOf('/') + 1) || 'index.html';
    return pageConfig[page];
}

// Initialiser les éléments dès que possible
function initializeElements() {
    const config = getCurrentPageConfig();
    if (!config) return;

    const isExternalEntry = !document.referrer.includes(window.location.host);

    if (isExternalEntry) {
        // Pour une entrée externe, tout est initialement invisible
        const elements = {
            top: document.querySelector(config.top),
            center: document.querySelector(config.center),
            bottom: document.querySelector(config.bottom)
        };

        Object.values(elements).forEach(el => {
            if (el) {
                el.style.opacity = '0';
                el.classList.add('fade-initial');
            }
        });
    } else {
        // Pour la navigation interne, seul le contenu central est invisible
        const center = document.querySelector(config.center);
        if (center) {
            center.classList.add('fade-initial');
        }
    }
}

// Appeler l'initialisation immédiatement
initializeElements();

// Animation d'apparition
function fadeInUi() {
    const config = getCurrentPageConfig();
    if (!config) return;

    const isExternalEntry = !document.referrer.includes(window.location.host);

    const elements = {
        top: document.querySelector(config.top),
        center: document.querySelector(config.center),
        bottom: document.querySelector(config.bottom)
    };

    // Préparer les éléments pour l'animation
    Object.values(elements).forEach(el => {
        if (el) {
            el.classList.add('fade-transition');
            el.style.opacity = '0';
            el.style.transform = 'translateY(20px)';
        }
    });

    if (isExternalEntry) {
        // Pour une entrée externe, animation progressive
        // Header et ses enfants
        setTimeout(() => {
            if (elements.top) {
                elements.top.style.opacity = '1';
                elements.top.style.transform = 'translateY(0)';
                elements.top.classList.remove('fade-initial');

                // Animer les enfants du header avec un délai
                const headerLinks = elements.top.querySelectorAll('a');
                headerLinks.forEach((link, index) => {
                    setTimeout(() => {
                        link.style.opacity = '1';
                        link.style.transform = 'translateY(0)';
                    }, index * 100); // 100ms de délai entre chaque lien
                });
            }
        }, 800);
        
        // Contenu central et ses enfants
        setTimeout(() => {
            if (elements.center) {
                elements.center.style.opacity = '1';
                elements.center.style.transform = 'translateY(0)';
                elements.center.classList.remove('fade-initial');

                // Animer les enfants selon la page
                const bentoItems = elements.center.querySelectorAll('.bento-item');
                if (bentoItems.length > 0) {
                    // Si nous sommes sur la page about avec le bento-grid
                    bentoItems.forEach((item, index) => {
                        setTimeout(() => {
                            item.style.opacity = '1';
                            item.style.transform = 'translateY(0)';
                        }, index * 150 + 200); // 150ms de délai entre chaque item, commençant 200ms après le parent
                    });
                } else {
                    // Pour les autres pages
                    const children = elements.center.children;
                    Array.from(children).forEach((child, index) => {
                        setTimeout(() => {
                            child.style.opacity = '1';
                            child.style.transform = 'translateY(0)';
                        }, index * 150); // 150ms de délai entre chaque enfant
                    });
                }
            }
        }, 1200);

        // Footer
        setTimeout(() => {
            if (elements.bottom) {
                elements.bottom.style.opacity = '1';
                elements.bottom.style.transform = 'translateY(0)';
                elements.bottom.classList.remove('fade-initial');
            }
        }, 1600);
    } else {
        // Pour la navigation interne
        if (elements.top) {
            elements.top.style.opacity = '1';
            elements.top.style.transform = 'translateY(0)';
            elements.top.classList.remove('fade-initial');
        }
        if (elements.bottom) {
            elements.bottom.style.opacity = '1';
            elements.bottom.style.transform = 'translateY(0)';
            elements.bottom.classList.remove('fade-initial');
        }

        // Animer uniquement le contenu central et ses enfants
        if (elements.center) {
            setTimeout(() => {
                elements.center.style.opacity = '1';
                elements.center.style.transform = 'translateY(0)';
                elements.center.classList.remove('fade-initial');

                // Animer les enfants avec un délai
                const bentoItems = elements.center.querySelectorAll('.bento-item');
                if (bentoItems.length > 0) {
                    // Si nous sommes sur la page about avec le bento-grid
                    bentoItems.forEach((item, index) => {
                        setTimeout(() => {
                            item.style.opacity = '1';
                            item.style.transform = 'translateY(0)';
                        }, index * 150 + 200); // 150ms de délai entre chaque item, commençant 200ms après le parent
                    });
                } else {
                    // Pour les autres pages
                    const children = elements.center.children;
                    Array.from(children).forEach((child, index) => {
                        setTimeout(() => {
                            child.style.opacity = '1';
                            child.style.transform = 'translateY(0)';
                        }, index * 150 + 100); // 150ms de délai entre chaque enfant, commençant 100ms après le parent
                    });
                }
            }, 100);
        }
    }
}

// Animation de disparition
function fadeOutUi() {
    const config = getCurrentPageConfig();
    if (!config) return;

    // On ne fait disparaître que le contenu central, pas le header ni le footer
    const center = document.querySelector(config.center);
    if (center) {
        center.classList.add('fade-transition');
        center.offsetHeight; // Force un reflow
        center.style.opacity = '0';
        center.style.transform = 'translateY(20px)';
        center.classList.add('fade-initial');
    }
}

// Attendre que le DOM soit complètement chargé
document.addEventListener('DOMContentLoaded', () => {
    // Ajouter un délai avant de lancer l'animation d'apparition
    setTimeout(() => {
        fadeInUi();
    }, 500); // Délai de 500ms avant le début de l'animation

    // Gérer les clics sur les liens
    document.querySelectorAll('a[href]').forEach(link => {
        link.addEventListener('click', function(e) {
            // Ignorer les liens externes, ancres, mailto et tel
            if (
                this.getAttribute('target') === '_blank' || 
                this.getAttribute('href').startsWith('#') ||
                this.getAttribute('href').startsWith('mailto:') ||
                this.getAttribute('href').startsWith('tel:')
            ) {
                return;
            }

            e.preventDefault();
            const destination = this.getAttribute('href');

            // Lancer l'animation de disparition
            threeAnimation.fadeOutPoints(fadeOutDuration);
            fadeOutUi();
            
            // Changer de page après le délai
            setTimeout(() => {
                window.location.href = destination;
            }, transitionTime);
        });
    });
});